import "./styles.css";
import React, { Component } from "react";
export default class App extends Component {
  constructor() {
    super();
    this.state = {
      data: [
        { id: "1", name: "bar", age: "20" },
        { id: "1", name: "fo", age: "22" },
        { id: "1", name: "fofo", age: "26" }
      ]
    };
  }
  render() {
    return (
      <div className="App">
        <h6>Hello CodeSandbox</h6>
        <table>
          <tbody>
            {this.state.data.map((person, i) => {
              return <TableRow key={i} props={person} />;
            })}
          </tbody>
        </table>
      </div>
    );
  }
}
class Header extends Component {
  render() {
    return <div> Header </div>;
  }
}

class TableRow extends Component {
  render() {
    return (
      <div>
        <tr>
          <td> {this.props.props.id} </td>
          <td> {this.props.props.name} </td>
          <td> {this.props.props.age} </td>
        </tr>
      </div>
    );
  }
}
